<template lang="pug">
  div(v-show="!this.$store.state.isLoading")
    .info-header
      router-link.white-space(to="/")  WhiteSpace
      .info-header-main(:style="{ backgroundImage: `url(${room.imageUrl[0]})` }" @click="lightBoxShow=true")
      .info-header-sub(@click="lightBoxShow=true")
        .info-header-sub-top(:style="{ backgroundImage: `url(${room.imageUrl[1]})` }")
        .info-header-sub-bottom(:style="{ backgroundImage: `url(${room.imageUrl[2]})` }")
    .info-main
      .info-main-data
        .info-main-data-intro
          .info-main-data-intro-left
            h1 {{ room.name }}
            p {{ room.descriptionShort.GuestMin }} ~ {{ room.descriptionShort.GuestMax }} 
            p {{ room.descriptionShort.Bed[0] }}
            p {{ room.descriptionShort['Private-Bath'] }} 
            p {{ room.descriptionShort.Footage }} 
            p.info-main-data-intro-description {{ room.description }}
            p(style="font-weight: bold")   
            .checkInOut
              .checkInOut-checkIn
                .checkInOut-head Check In
                .checkInOut-time {{ room.checkInAndOut.checkInEarly }}  {{ room.checkInAndOut.checkInLate }}
              .checkInOut-checkOut
                .checkInOut-head Check out
                .checkInOut-time {{ room.checkInAndOut.checkOut }}
            .info-main-data-intro-amenities
              .amenities
                img(src="@/assets/info_icon/wifi.svg" v-if="room.amenities['Wi-Fi']")
                img(src="@/assets/info_icon/wifi_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Wi-Fi']}") Wi-Fi
              .amenities
                img(src="@/assets/info_icon/phone.svg" v-if="room.amenities['Television']")
                img(src="@/assets/info_icon/phone_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Television']}") 
              .amenities
                img(src="@/assets/info_icon/mountain-range.svg" v-if="room.amenities['Great-View']")
                img(src="@/assets/info_icon/mountain-range_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Great-View']}") 
              .amenities
                img(src="@/assets/info_icon/breakfast.svg" v-if="room.amenities['Breakfast']")
                img(src="@/assets/info_icon/breakfast_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Breakfast']}") 
              .amenities
                img(src="@/assets/info_icon/breeze.svg" v-if="room.amenities['Air-Conditioner']")
                img(src="@/assets/info_icon/breeze_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Air-Conditioner']}") 
              .amenities
                img(src="@/assets/info_icon/no-smoke-symbol.svg" v-if="room.amenities['Smoke-Free']")
                img(src="@/assets/info_icon/no-smoke-symbol_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Smoke-Free']}") 
              .amenities
                img(src="@/assets/info_icon/bar.svg" v-if="room.amenities['Mini-Bar']")
                img(src="@/assets/info_icon/bar_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Mini-Bar']}") Mini Bar
              .amenities
                img(src="@/assets/info_icon/wifi.svg" v-if="room.amenities['Refrigerator']")
                img(src="@/assets/info_icon/wifi_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Refrigerator']}") 
              .amenities
                img(src="@/assets/info_icon/crawling-baby-silhouette.svg" v-if="room.amenities['Child-Friendly']")
                img(src="@/assets/info_icon/crawling-baby-silhouette_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Child-Friendly']}") 
              .amenities
                img(src="@/assets/info_icon/room_service.svg" v-if="room.amenities['Room-Service']")
                img(src="@/assets/info_icon/room_service_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Room-Service']}") Room Service
              .amenities
                img(src="@/assets/info_icon/wifi.svg" v-if="room.amenities['Sofa']")
                img(src="@/assets/info_icon/wifi_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Sofa']}") 
              .amenities
                img(src="@/assets/info_icon/dog.svg" v-if="room.amenities['Pet-Friendly']")
                img(src="@/assets/info_icon/dog_no.svg" v-else)
                span(:class="{'gray': !room.amenities['Pet-Friendly']}") 
          .info-main-data-intro-right
            .normalPrice NT.{{ room.normalDayPrice }}
            .day (~)
            .holidayPrice NT.{{ room.holidayPrice }}
            .day (~)
      .info-main-date
        Datepicker(:inline="true" v-model="reservate.start" :language="zh" :disabledDates="disabledDates" :minimumView="'day'" :highlighted="highlighted" :maximumView="'day'")
        .reservation(@click="reservateShow = true") 
    //- 
    .light-box(:class="{'open': lightBoxShow}" @click.self="lightBoxShow=false")
      .light-box-prev(@click="changeLightBoxPicture('prev')")
        img(src="@/assets/index_icon/arrow_left_2x.png")
      .light-box-picture
        img(:src="room.imageUrl[lightBoxIndex]")
        .light-box-info
          p {{room.name}}
          p {{ lightBoxIndex + 1 }} / {{ room.imageUrl.length }}
      .light-box-next(@click="changeLightBoxPicture('next')")
        img(src="@/assets/index_icon/arrow_right_2x.png")
    //- 
    .reservation-modal(:class="{'open': reservateShow}" @click.self="reservateShow=false, reservateStatus.doing=true")
      .reservation-modal-card(v-if="reservateStatus.doing")
        .reservation-modal-card-wrap
          .reservation-modal-card-title 
          p(style="font-weight: bold; margin-top: .5rem; margin-bottom: 2rem")   
          .form-group
            label(for="#form-name") 
            input(id="form-name" type="text" v-model="bookingForm.name")
          .form-group
            label(for="#form-phone") 
            input(id="form-phone" type="tel" v-model="bookingForm.tel")
          .form-group
            label 
            Datepicker(:language="zh" v-model="reservate.start" :format="'yyyy/MM/dd'" :highlighted="highlighted" :disabledDates="disabledDates" :minimumView="'day'" :maximumView="'day'")
            div ~
            Datepicker(:language="zh" v-model="reservate.end" :format="'yyyy/MM/dd'" :highlighted="highlighted" :disabledDates="disabledDates" :minimumView="'day'" :maximumView="'day'")
        .reservation-modal-time-slot(v-show="weekday !== 0 || holiday !== 0")
          .modal-time-slot(v-show="weekday !== 0")
            .time-slot 
            .night {{ weekday }}
          .modal-time-slot(v-show="holiday !== 0")
            .time-slot 
            .night {{ holiday }}
        .reservation-modal-price(v-show="weekday !== 0 || holiday !== 0") =
          span NT.{{ weekday * room.normalDayPrice + holiday * room.holidayPrice  }}
        .reservation-modal-btns
          .reservation-modal-cancel.btn(@click="cancelReservation") 
          .reservation-modal-confirm.btn(:class="{disable: confirmDisable}" @click="bookRoom") 
      .reservation-modal-card(v-else)
        .reservation-modal-card-wrap(style="display: flex; flex-direction: column;" v-if="reservateStatus.success")
          .reservation-modal-card-title 
          p(style="font-weight: bold; margin-top: .5rem; margin-bottom: 2rem")   
          img(src="@/assets/info_icon/tick-inside-circle.svg" style="width: 60px; align-self: center")
          .reservation-modal-confirm.btn(style="margin: 40px 0 0 auto;" @click="cancelReservation(), reservateShow=false, reservateStatus.doing = true, getRoom()") 
        .reservation-modal-card-wrap(style="display: flex; flex-direction: column;" v-else)
          .reservation-modal-card-title 
          p(style="font-weight: bold; margin-top: .5rem; margin-bottom: 2rem")   
          p 
          .reservation-modal-confirm.btn(style="margin: 40px 0 0 auto;" @click="cancelReservation, reservateStatus.doing = true") 
</template>

<script>
import axios from '../axios'
import Datepicker from 'vuejs-datepicker'
import { zh } from 'vuejs-datepicker/dist/locale'
export default {
  components: {
    Datepicker
  },
  data () {
    return {
      room: {
        imageUrl: [],
        descriptionShort: {
          Bed: [],
          Footage: 0,
          GuestMax: 0,
          GuestMin: 0,
          'Private-Bath': 0
        },
        amenities: {},
        checkInAndOut: {}
      },
      booking: [],
      highlighted: {
        dates: []
      },
      lightBoxShow: false,
      lightBoxIndex: 0,
      zh: zh,
      disabledDates: {
        customPredictor: function (date) {
          // disables every day of day less now
          const dateNow = new Date()
          if (date.getFullYear() < dateNow.getFullYear()) return true
          if (date.getFullYear() <= dateNow.getFullYear() && date.getMonth() < dateNow.getMonth()) return true
          if (date.getFullYear() <= dateNow.getFullYear() && date.getMonth() <= dateNow.getMonth()) {
            if (date.getDate() <= dateNow.getDate()) return true
          }
        }
      },
      reservateShow: false,
      reservate: {
        start: '',
        end: ''
      },
      reservateStatus: {
        doing: true,
        success: false
      },
      bookingForm: {
        name: '',
        tel: '',
        date: []
      },
      confirmDisable: true,
      holiday: 0,
      weekday: 0
    }
  },
  methods: {
    getRoom (id = this.$route.query.id) {
      this.$store.commit('LOADING', true)
      axios.getRoom(id).then(res => {
        this.booking = res.data.booking
        this.room = res.data.room[0]
        this.$store.commit('LOADING', false)
        this.highlighted.dates = res.data.booking.map(day => new Date(day.date))
      })
    },
    changeLightBoxPicture (type) {
      switch (type) {
        case 'next':
          if (this.lightBoxIndex < this.room.imageUrl.length - 1) {
            this.lightBoxIndex++
          } else {
            this.lightBoxIndex = 0
          }
          break
        case 'prev':
          if (this.lightBoxIndex > 0) {
            this.lightBoxIndex--
          } else {
            this.lightBoxIndex = this.room.imageUrl.length - 1
          }
          break
        default:
          break
      }
    },
    cancelReservation () {
      this.reservate = {
        start: '',
        end: ''
      }
      this.bookingForm = {
        name: '',
        tel: '',
        date: []
      }
      this.holiday = 0
      this.weekday = 0
      this.reservateShow = false
    },
    bookRoom () {
      if (this.confirmDisable) return
      axios.bookRoom(this.$route.query.id, this.bookingForm).then(res => {
        if (res.data.success) {
          this.reservateStatus.doing = false
          this.reservateStatus.success = true
        } else {
          this.reservateStatus.doing = false
          this.reservateStatus.success = false
        }
      }).catch(err => {
        console.log(err)
      })
    }
  },
  watch: {
    lightBoxShow (val) {
      if (val) {
        this.$nextTick(() => {
          const img = document.querySelector('.light-box-picture img')
          img.style.height = (img.clientWidth / 1.5) + 'px'
        })
      }
    },
    reservate: {
      handler (item) {
        if (item.start === '' || item.end === '') return
        // item.start.getTime() >= item.end.getTime() ? this.confirmDisable = true : this.confirmDisable = false
        if (item.start.getTime() >= item.end.getTime()) {
          this.bookingForm.date = []
          this.holiday = 0
          this.weekday = 0
          return
        }
        const days = (item.end.getTime() - item.start.getTime()) / 86400000
        const timeArray = [item.start.getTime()]
        for (let i = 1; i < days; i++) {
          timeArray.push(new Date(timeArray[i - 1] + 86400000).getTime())
        }
        this.holiday = 0
        this.weekday = 0
        this.bookingForm.date = []
        timeArray.forEach(daytime => {
          const day = new Date(daytime).getDay()
          this.bookingForm.date.push(new Date(daytime).toLocaleDateString('zh-TW', { year: 'numeric', month: '2-digit', day: '2-digit' }).replace(/\//gi, '-'));
          (day === 6 || day === 0) ? this.holiday++ : this.weekday++
        })
      },
      deep: true
    },
    bookingForm: {
      handler (val) {
        if (val.name === '' || val.tel === '' || val.date.length === 0) {
          this.confirmDisable = true
        } else {
          this.confirmDisable = false
        }
      },
      deep: true
    }
  },
  created () {
    // console.log(this.$route.query.id)
    this.getRoom(this.$route.query.id)
  }
}
</script>
